export const revolutionary = function(ctx){
	ctx.font = "32px Gotham Rounded";
	ctx.fillStyle = 'white';
	var lineHeight = 16,
		fontSize = 32;
	ctx.fillText("moneyamigo - the new revolutionary", 0, fontSize);
	ctx.fillText("way to manage your money - your way", 0, fontSize * 2 + lineHeight);
}


