var $ = jQuery;

export const youchoose = function(ctx, canvas) {

	var lineHeight = 16,
		fontSize = 156,
		initial = fontSize;

	var lines = [
		'you choose. you can´t go wrong',
	];

	ctx.font = "bold " + fontSize + "px Avenir LT Std";
	ctx.fillStyle = 'white';

	$.each(lines, function(key, line){
		ctx.fillText(line, 0, ((fontSize + lineHeight) * key) + initial);
	});
}


